import React, { Component } from 'react';
import '../styles/InputSuggest.css';

class InputSuggest extends Component {
    state = {
        inputValue: '',
        suggestions: [],
        isSuggestionOpen: false,
        suggestionSelected: {}
    }

    onChangeInput = (e) => {
        const { list } = this.props;
        const text = e.target.value;
        const inputValue = text.trim().toLowerCase();
        const inputLength = inputValue.length;
        let suggestions = [];
        if (list.length > 0) {
            suggestions = list.filter(lang =>
                lang.city_name.toLowerCase().slice(0, inputLength) === inputValue
            );
        }
        //
        this.setState({
            inputValue: text,
            suggestions: suggestions,
            isSuggestionOpen: true
        })

    }

    onSuggestionSelected = (suggestionItem) => e => {
        const { getSelectedItem } = this.props;
        getSelectedItem(suggestionItem);
        //
        this.setState({
            inputValue: suggestionItem.city_name,
            isSuggestionOpen: false
        })
    }

    render() {
        const { label } = this.props;
        const { inputValue, suggestions, isSuggestionOpen } = this.state;
        return (
            <div>
                <p className="input-suggest__label">{label}</p>
                <input className="input-suggest__input-text" type="text" value={inputValue} onChange={this.onChangeInput} />
                {isSuggestionOpen &&
                    <div className='input-suggest__container-suggestion'>
                        {suggestions.length > 0 ? suggestions.map((item) => {
                            return (
                                <div className='input-suggest__suggestion-item'>
                                    <p onClick={this.onSuggestionSelected(item)}>{item.city_name}</p>
                                </div>
                            )
                        }) :
                            <p>Kota tidak ditemukan</p>
                        }
                    </div>
                }
            </div>
        )
    }
}

export default InputSuggest;